import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'

Vue.use(Router)

export default new Router({
	base: process.env.BASE_URL,
	routes: [{
			path: '/',
			name: 'login',
			component: Login
		},
		{
			path: '/mains',
			name: 'mains',
			component: () => import('./views/Mains.vue'),
			meta: {
				title: '主页',
				isLogin: true
			},
			children: [{
					path: 'business',
					name: 'business',
					component: () => import('./views/Business/Business.vue'),
					meta: {
						title: '体检业务管理',
						icon: 'el-icon-yibiaopan1',
						isshow: true,
						isLogin: true
					},
					children:[
						{
							path: 'examine',
							name: 'examine',
							component: () => import('./views/Business/Examine.vue'),
							meta: {
								title: '体检审核',
								isshow: true,
								isLogin: true,
								demand:true
							}
		
						},{
							path: 'pay',
							name: 'pay',
							component: () => import('./views/Business/Pay.vue'),
							meta: {
								title: '缴费打印',
								isshow: true,
								isLogin: true
							}
		
						},{
							path: 'examines',
							name: 'examines',
							component: () => import('./views/Business/Examines.vue'),
							meta: {
								title: '体检信息管理',
								isshow: true,
								isLogin: true
							}
		
						},{
							path: 'pays',
							name: 'pays',
							component: () => import('./views/Business/Pays.vue'),
							meta: {
								title: '缴费信息管理',
								isshow: true,
								isLogin: true
							}
		
						}
					]

				},
				{
					path: 'form',
					name: 'form',
					component: () => import('./views/Form/Form.vue'),
					meta: {
						title: '统计报表',
						icon: 'el-icon-lingyong',
						isshow: true,
						isLogin: true
					},
					children:[
						{
							path: 'number',
							name: 'number',
							component: () => import('./views/Form/Number.vue'),
							meta: {
								title: '体检人数统计',
								isshow: true,
								isLogin: true
							}
		
						},{
							path: 'charge',
							name: 'charge',
							component: () => import('./views/Form/Charge.vue'),
							meta: {
								title: '设备收费统计',
								isshow: true,
								isLogin: true
							}
		
						},{
							path: 'result',
							name: 'result',
							component: () => import('./views/Form/Result.vue'),
							meta: {
								title: '体检审核结果',
								isshow: true,
								isLogin: true
							}
		
						},{
							path: 'time',
							name: 'time',
							component: () => import('./views/Form/Time.vue'),
							meta: {
								title: '按时间段统计体检人数',
								isshow: true,
								isLogin: true
							}
		
						}
					]
				},
				{
					path: 'video',
					name: 'video',
					component: () => import('./views/Video/Video.vue'),
					meta: {
						title: '视频回放',
						icon: 'el-icon-1',
						isshow: true,
						isLogin: true
					}
				},
				{
					path: 'password',
					name: 'password',
					component: () => import('./components/Password.vue'),
					meta: {
						title: '修改密码',
						isshow: false,
						isLogin: true
					}
				},
				{
					path: 'print',
					name: 'print',
					component: () => import('./components/Print.vue'),
					meta: {
						title: '打印',
						isshow: false,
						isLogin: true
					}
				},
				{
					path: 'index',
					name: 'index',
					component: () => import('./views/Index.vue'),
					meta: {
						title: '主页',
						isshow: false,
						isLogin: true
					}
				}
			]
		}
	]
})
